import { useState } from 'react';
import '../App.css';
// import '../js/bus.js';
import Rebus from '../json/data.json';

export default function Bus(){

    let stopArr = [], stopName=[], stopId=[], roadImg=[];
    Rebus.BUSSTOP.map( (stop,k) => ( stopArr.push(stop['stopImg']) )) // 
    Rebus.BUSSTOP.map( (stop,k) => ( stopName.push(stop['stop']))) //  
    Rebus.BUSSTOP.map( (stop,k) => ( stopId.push(stop['stopID']))) //  id
    Rebus.BUSSTOP.map( (stop,k) => ( roadImg.push(stop['roadImg']))) // roadImg

    let [stop,stopChange] = useState(0) //  
    let [page, pageChange] = useState(1) //
    let [sName, sNameC] = useState(0) //  
    let [id, idC] = useState(0) // id
    let [road, roadC] = useState(0) // 

    function next(){
        if(stop<stopArr.length -1){
            stopChange(stop+1)
            pageChange(page+1)
            sNameC(sName+1)
        }else{
            stopChange(0)
            pageChange(1)
            sNameC(0)
        }
    }
    function prev(){
        stopChange(stop-1)
        pageChange(page-1)
        sNameC(sName-1)
        if( page == 1 ){
            stopChange(8)
            pageChange(9)
            sNameC(8)
        }
    }
    const pop = document.querySelector('.pop')
    // function popup(){
    //     pop.classList.add('active')
    // }

    function roadNext(){
        if(sName == 8) return;
        sNameC(sName+1)
        idC(id+1)
        roadC(road+1)
        pageChange(page+1)
        stopChange(stop+1)
    }
    function roadPrev(){
        if(sName == 0) return;
        sNameC(sName-1)
        idC(id-1)
        roadC(road-1)
        pageChange(page-1)
        stopChange(stop-1)
    }
    function stoppop(){
        pop.style.display = 'block'
    }
    function closepop(){
        pop.style.display = 'none'
    }

    let hoverImg = [], busImg=[];
    Rebus.BUS.map( (img,k) => ( hoverImg.push( img['hover']) ))
    Rebus.BUS.map( (img,k) => ( busImg.push( img['img']) ))
    
    return(
        <>
            <div className="busIntro">
                <figure><img src={require("../img/regreenbus_intro_logo.png").default} alt=""/></figure>
                <h2>, <b> </b> </h2>
                <div><img src={require("../img/regreenbus_intro_logo_bus.png").default} alt=""/></div>
                <iframe width="763" height="430" src="https://www.youtube.com/embed/HkRKW2f1hPU" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                <p>         <br/>      ,    .<br/>    .    .</p>
            </div>
            <div className="busStop">
                <div className="busStopTit">
                    <article>
                        <h2><b>RE:GREEN</b> ?</h2>
                        <p>,      , ,  <br/>  ( )          </p>
                    </article>
                    <p><img src={require("../img/regreenbus_info_img.png").default} alt=""/></p>
                </div>
                <div className="busImg">
                    <p><img src={require("../img/regreenbus_info_bg.png").default} alt=""/></p>
                    {
                        Rebus.BUS.map( (re,k) => (
                            <div key={k}>
                                <img src={ require(`../img/${hoverImg[k]}`).default} alt="" className={re['hoverName']}/>
                                <img src={ require(`../img/${busImg[k]}`).default} alt="" className={re['imgName']}/>
                                <button className={re['btnName']}>+</button>
                            </div>
                        ))
                    }
                </div>
            </div>
            <div className="adCopy">
                <div className="adTit">
                    <h2><b></b>     </h2>
                    <p>     (6.2km) Green Road<br/>       .</p>
                    <div> &nbsp;&nbsp;<button>+</button>&nbsp;&nbsp; ,     .</div>
                </div>
                <div className="adBus">
                    <p><img src={require("../img/regreenbus_road_img.jpg").default} alt=""/></p>
                    <div className="adBtn">
                        {
                            Rebus.BUSSTOP.map( (sb,k) => (
                                <p className={sb['stopName']} key={k}>
                                    <button onClick={stoppop}>+</button>
                                </p>
                            ))
                        }
                    </div>
                </div>
                <div className="adSlide">
                    <p><span></span><span></span><span></span><span></span><span></span></p>
                    <div><img src={require(`../img/${stopArr[stop]}`).default} alt=""/></div>
                    <p className="sltit">{stopName[sName]}</p>
                </div>
                <p className="stopBtn">
                    <button onClick={prev}></button>
                    <strong>{page} / 9</strong>
                    <button onClick={next}></button>
                </p>
                <div className="pop">
                    <div>
                        <p className="poptit">
                            <button onClick={roadPrev}></button>
                            <span>
                                <div>{stopName[sName]}</div>
                                <p>{stopId[id]}</p>
                            </span>
                            <button onClick={roadNext}></button>
                        </p>
                        <div className="popimg">
                            <p><img src={ require(`../img/${roadImg[road]}`).default } alt="" onClick={closepop}/></p>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}